import React from 'react';

import ColumnsSlider from './ColumnsSlider'

function ListsPage({ lists, listOrder, onDragEnd, allTasks, handleAddList, handleEditListTitle, handleDeleteList }) {
    return (
        <div 
            className="col-12"
            style={{height: "100vh",
                    width: "100%",
                    padding: 0,
                    minHeight: 850
            }} 
            id="Lists"
        >

        <div 
            className="row pb-5"
            style={{paddingTop: "5.5rem"}}
        >
            <div className="col-6 col-md-8 col-lg-10 me-auto ps-3">
                
                {/* <SearchBar />   */}
                <div
                    className="mx-5 px-3"
                    style={{
                            background: "white",
                            borderRadius: 19,
                            display: "flex"
                        }}>
                    <img className="d-inline-blcok mx-3 search-icon" src="data:image/svg+xml;utf8;base64," />
                    <input
                        className="search bg-0" type="text" placeholder="search" style={{borderRadius: 19,border: 0, height: 40, display: "inline-flex", flexGrow: 1, zIndex: 99999}}/>
                </div>

            </div>
            <div className="col-2">

            </div>
        </div>

        <div 
            className="row"
            style={{
                height: "80%",
                overflow: "hidden",
            }}
        >
            <div 
                className="col w-100"
                style={{overflow: "hidden"}}
            >

                <ColumnsSlider
                    lists={lists}
                    listOrder={listOrder}
                    onDragEnd={onDragEnd}
                    allTasks={allTasks}
                    handleAddList={handleAddList}
                    handleEditListTitle={handleEditListTitle}
                    handleDeleteList={handleDeleteList}
                />
                {/* overflow scroll X */}


            </div>









        </div>
    </div>

    );
}

export default ListsPage;